.Grid {
  border-collapse: collapse;
  border-spacing: 0;
  white-space: nowrap;
  --margin: 10px;
  margin: 0 var(--margin) var(--margin) 0;
}
.Grid th:first-child {
  /* width: 90px; */
  padding-left: 10px;
  text-align: right;
}
.Grid thead {
  text-align: right;
  vertical-align: bottom;
  white-space: nowrap;
}
.Grid thead div {
  -webkit-writing-mode: vertical-rl;
      -ms-writing-mode: tb-rl;
          writing-mode: vertical-rl;
  text-orientation: sideways-right;
  text-align: right;
  vertical-align: bottom;
  padding-top: 10px;
  line-height: 2em;
}
code,
.Grid th {
  padding: 0;
  font-family: Consolas, "Andale Mono WT", "Andale Mono", "Lucida Console", "Lucida Sans Typewriter", "DejaVu Sans Mono", "Bitstream Vera Sans Mono", "Liberation Mono", "Nimbus Mono L", Monaco, "Courier New", Courier, monospace;
}
.Grid td {
  padding: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.Grid thead th:first-child {
  border-style: none;
}
.Grid th:first-child {
  border-right: var(--cell-border2) solid var(--border-hl2);
  padding-right: 4px;
}
.Grid thead th {
  border-bottom: var(--cell-border2) solid var(--border-hl2);
  padding-bottom: 4px;
}
.Grid td:last-child {
  border-right: var(--cell-border2) solid var(--border-hl1);
}
.Grid tr:last-child td {
  border-bottom: var(--cell-border2) solid var(--border-hl1);
}




